<template>
	<div class="mb-3 block mt-8">
		<div class="bg-gray-300 text-center px-8 py-16">
			<div
				class="text-base mb-8 text-center break-words"
			>{{RESULT_PAGE_1.type}} пожертвование в размере {{ count }} руб.</div>
			<div
				class="bg-gray-800 text-white relative flex justify-center items-center w-full max-w-lg h-16 cursor-pointer mx-auto"
				@click.once="sendData(({name:'step_4'}))"
			>Оплатить картой</div>
		</div>

		<loading :active.sync="visible" :can-cancel="false" :height="40" :width="40" :opacity="0.80"></loading>

		<router-link :to="{name: 'step_2'}">
			<button class="mt-4 cursor-pointer text-sm text-blue-900 p-0 h-12 outline-none">Назад</button>
		</router-link>

		<!--Другие способы-->
		<div class="mt-8">
			<span
				class="mt-4 cursor-pointer text-sm text-gray-800 border-b border-gray-400 w-auto p-0"
			>Другие способы пожертвования</span>
		</div>
		<!--Другие способы-->
	</div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import sendDataService from '@/services/sendData'
import sendYandexService from '@/services/sendYKData'
import crypto from 'crypto'
import Loading from 'vue-loading-overlay'
import 'vue-loading-overlay/dist/vue-loading.css'
import _ from 'lodash'

export default {
	name: 'Step3',
	data() {
		return {
			customerID: {},
			visible: false,
		}
	},
	computed: {
		...mapGetters([
			'RESULT_PAGE_1',
			'RESULT_PAGE_2',
			'STEP_2',
			'STEP_3',
			'STEP_4',
			'PAYMENT_TOKEN',
		]),
		count() {
			let sum = this.$store.state.RESULT_PAGE_1.sum
			let result = String(sum).replace(/(\d)(?=(\d{3})+([^\d]|$))/g, '$1 ')
			return result
		},
	},
	methods: {
		...mapActions([
			'SET_RESULT_PAGE_1',
			'SET_RESULT_PAGE_2',
			'SET_STEP_2',
			'SET_STEP_3',
			'SET_STEP_4',
			'SET_PAYMENT_TOKEN',
		]),
		async sendData(route) {
			this.visible = true

			function findValue(obj, key) {
				let k, v
				for ([k, v] of Object.entries(obj)) {
					if (k == key) return v
					if (typeof v === 'object' && v !== null) {
						let found = findKey(v, key)
						if (found) return found
					}
				}
			}
			function findKey(obj, key) {
				let keysArray = Object.keys(obj)
				for (let i = 0; i < keysArray.length; i++) {
					if (key === keysArray[i]) {
						return true
					}
				}
			}
			function findID(id, checksum) {
				for (let i = 0; i < id.records.length; i++) {
					const dbChecksum = id.records[i].fields.CheckSum
					if (checksum === dbChecksum) {
						return id.records[i].id
					}
				}
			}
			function paymentType(type) {
				if (type === 'Ежемесячное') {
					return true
				} else {
					return false
				}
			}
			function checkData(obj_1, obj_2) {
				delete obj_1.fields['Transactions']
				let fields_1 = Object.keys(obj_1.fields)
				let fields_2 = Object.keys(obj_2.records[0].fields)
				if (fields_2.length !== fields_1.length) {
					let result = _.omit(obj_2.records[0].fields, fields_1)
					return result
				}
			}

			try {
				// Переменные
				let id = ''
				let step_1 = this.$store.state.RESULT_PAGE_1
				let step_2 = this.$store.state.RESULT_PAGE_2
				let checksum = crypto
					.createHash('md5')
					.update(step_2.name + step_2.surname + step_2.email)
					.digest('Hex')

				let localCustomerMd5 = findKey(localStorage, checksum)
				let localCustomerID = findValue(localStorage, checksum)
				let responseID = (await sendDataService.get('Customers')).data
				let dbID = findID(responseID, checksum)
				let checkDataByID = (await sendDataService.get(`Customers/${dbID}`))
					.data

				// Проверка LocalStorage, проверка CRM
				if (localCustomerMd5 && localCustomerID) {
					id = localCustomerID
					let result = {
						records: [
							{
								id: id,
								fields: {
									Имя: step_2.name,
									Фамилия: step_2.surname,
									'E-mail': step_2.email,
									Телефон: step_2.phone,
									'Дата рождения': step_2.bdata,
									Офферта: step_2.checkboxes.secagree,
									Поздравление: step_2.checkboxes.bdagree,
									Рассылка: step_2.checkboxes.subsagree,
									Публикация: step_2.checkboxes.pubagree,
									CheckSum: checksum,
								},
							},
						],
					}
					let updateData = {
						records: [
							{
								id: id,
								fields: { ...checkData(checkDataByID, result) },
							},
						],
					}
					if (updateData !== undefined) {
						await sendDataService.patch('Customers', updateData)
					}
				} else if (dbID) {
					id = dbID
					localStorage[checksum] = id
					let result = {
						records: [
							{
								id: id,
								fields: {
									Имя: step_2.name,
									Фамилия: step_2.surname,
									'E-mail': step_2.email,
									Телефон: step_2.phone,
									'Дата рождения': step_2.bdata,
									Офферта: step_2.checkboxes.secagree,
									Поздравление: step_2.checkboxes.bdagree,
									Рассылка: step_2.checkboxes.subsagree,
									Публикация: step_2.checkboxes.pubagree,
									CheckSum: checksum,
								},
							},
						],
					}
					let updateData = {
						records: [
							{
								id: id,
								fields: { ...checkData(checkDataByID, result) },
							},
						],
					}
					if (updateData !== undefined) {
						await sendDataService.patch('Customers', updateData)
					}
				} else {
					// Добавляем данные в таблицу Customers
					let result = {
						records: [
							{
								fields: {
									Имя: step_2.name,
									Фамилия: step_2.surname,
									'E-mail': step_2.email,
									Телефон: step_2.phone,
									'Дата рождения': step_2.bdata,
									Офферта: step_2.checkboxes.secagree,
									Поздравление: step_2.checkboxes.bdagree,
									Рассылка: step_2.checkboxes.subsagree,
									Публикация: step_2.checkboxes.pubagree,
									CheckSum: checksum,
								},
							},
						],
					}
					let responseCustomer = (
						await sendDataService.post('Customers', result)
					).data
					id = responseCustomer.records[0].id
					checksum = responseCustomer.records[0].fields.CheckSum
					localStorage[checksum] = id
				}

				// console.log('Время выполнения = ', time)

				// Приводим тип платежа к Boolean
				let type = paymentType(step_1.type)
				// Формируем JSON для Яндекс.Касса
				let data = {
					amount: {
						value: step_1.sum,
						currency: 'RUB',
					},
					confirmation: {
						type: 'embedded',
					},
					capture: true,
					description: id,
					receipt: {
						customer: {
							full_name: step_2.name + ' ' + step_2.surname,
							email: step_2.email,
						},
						type: 'payment',
						send: 'true',
						items: [
							{
								description: `${step_1.type} пожертвование`,
								quantity: '1.00',
								amount: {
									value: step_1.sum,
									currency: 'RUB',
								},
								vat_code: '1',
								payment_mode: 'full_prepayment',
								payment_subject: 'payment',
							},
						],
					},
					refundable: true,
					save_payment_method: type,
				}

				// Отправляем платеж в Яндекс.Кассу
				let payment = (await sendYandexService.post('test', data)).data
				// console.log('sendData -> payment', payment)

				// Собираем данные для переменной во Vuex (под вопросом)
				let payload = {
					payment_id: payment.payment_id,
					user_id: payment.user_id,
					sum: step_1.sum,
					type: step_1.type,
					status: payment.status,
					confirmation_token: payment.confirmation_token,
				}

				let status = {
					pending: 'Платеж создан',
					succeeded: 'Платеж успешно завершен',
					canceled: 'Платеж отменен',
				}
				let statusForTransaction = findValue(status, payment.status)
				// console.log('sendData -> statusForTransaction', statusForTransaction)

				// Собираем данные для таблицы транзакций
				let transaction = {
					records: [
						{
							fields: {
								Жертвователь: [payment.user_id],
								'Тип пожертвования': step_1.type,
								'Сумма пожертвования': step_1.sum,
								'Статус платежа': statusForTransaction,
								'ID платежа': payment.payment_id,
								'Стасус оплаты': 'Не оплачен',
							},
						},
					],
				}
				// console.log("sendData -> transaction", transaction)
				let responseTransaction = (
					await sendDataService.post('Transactions', transaction)
				).data
				// console.log('sendData -> responseTransaction', responseTransaction)

				// Собираем данные для таблицы подписок
				let subsctiption = {
					records: [
						{
							fields: {
								'ID платежа': payment.payment_id,
								'ID транзакции': [responseTransaction.records[0].id],
							},
						},
					],
				}
				// console.log('sendData -> subsctiption', subsctiption)

				// Добавляем данные в таблицу Subsctiption
				if (type) {
					await sendDataService.post('Subscriptions', subsctiption)
				}

				this.$store.dispatch('SET_PAYMENT_TOKEN', payload)
				this.$store.dispatch('SET_STEP_4')
				this.$router.push(route)
			} catch (error) {
				console.log('error', error)
			}
		},
	},
	components: {
		Loading: Loading,
	},
}
</script>

